.footer {
    background-color: $color-sky;
    color: $white;
    padding-top: 28px;

    &__logo {
        float: right;
        max-width: 150px;
    }

    &__column {
        display: inline-block;
        margin-right: 50px;
        vertical-align: top;
        @media (max-width: $screen-xs-max) {
            margin-right: 45px;
            &:first-child {
                width: 100%;
                margin-right: 0;
            }
        }
    }

    &__nav {
        margin-top: 70px;
        @media (max-width: $screen-xs-max) {
            margin-bottom: 10px;
        }
    }

    &__heading {
        color: white;
        font-size: 16px;
        @include ffoFamily("Sunstroke Headline");
        font-weight: normal;
        @media (max-width: $screen-xs-max) {
            display: block;
            margin-top: 5px;
            margin-bottom: 5px;
        }
    }

    &__list {
        list-style: none;
    }

    &__item {
        padding-left: 0;
        text-indent: 0;
        font-size: 16px;
        > a { color: white; }
        &:before { display: none; }
    }

    &__info {
        position: relative;
        text-align: right;
        padding-bottom: 5px;
    }

    &__social {
        text-align: left;
        margin-bottom: 20px;
        @media (min-width: $screen-sm-min) {
            text-align: right;
        }
        > a {
            display: inline-block;
            margin-left: 5px;
            vertical-align: top;
            &:first-child { margin-left: 0; }
        }
    }

    .copyright {
        display: inline-block;
        font-size: 18px;
        @media (min-width: $screen-sm-min) {
            font-size: 13px;
        }
    }

    .back-to-top {
        display: inline-block;
        font-size: 18px;
        margin-left: 11px;
        color: white;
        &:after {
            margin-left:  5px;
            content: "^";
        }
    }

    &__page-controls {
        @media (max-width: $screen-xs-max) {
            text-align: center;
            margin-bottom: 20px;
        }
    }
}

 .additional {
    &__button {
        bottom: -6px;
        background: #fff;
        padding: 15px 25px 15px;
        border-top-right-radius: 18px;
        font-size: 16px;
        color: $color-briquette;
        left: -25px;
        cursor: pointer;
        display: block;
        position: relative;
        text-align: left;
        @media (min-width: $screen-sm-min) {
            text-align: right;
            display: inline-block;
            position: absolute;
            bottom: 0;
            border-top-left-radius: 18px;
        }
        &:after {
            content: "V";
            margin-left: 10px;
            @include ffoFamily();
            color: $color-ketchup;
        }
        &--is-active {
            &:after {
                content: "^";
            }
        }
    }

    &__collapse {
        display: none;
        font-size: 15px;
        &.in {
            display: block;
        }
        &.collapsing {
          position: relative;
          height: 0;
          overflow: hidden;
        }
        > .container{
            padding-top: 30px;
            padding-bottom: 30px;
        }
        p {
            font-size: 15px;
        }
    }
}
