.expanders {
margin-bottom : ($vertical-spacing); }

.expander {

.expander-trigger {
display : block;
padding: .5vw 0;
margin-bottom : ($vertical-spacing / 8);
border-bottom : $border-width solid $border-color;
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size);
color : $link-color;
cursor : pointer;
text-decoration : none;
user-select : none;

&::before {
position : relative;
top : 6px;
content : url($root + 'img/expander-icon-up.svg');
margin-right : ($gutter / 2); }

&:last-of-type {
border-bottom-width : 0; }
}

.expander-hidden {
&::before {
position : relative;
top : 6px;
content : url($root + 'img/expander-icon-down.svg'); }
}

.expander-hidden + .expander-content {
display : none; }
}

.expander-content {
padding-top : ($vertical-spacing / 2);

p {
margin-bottom : 0; }
}
