@-webkit-viewport {
width : device-width; }

@-moz-viewport {
width : device-width; }

@-ms-viewport {
width : device-width; }

@-o-viewport {
width : device-width; }

@viewport {
width : device-width; }

html {
background : $body-bg;
font-size : 62.5%;
-webkit-font-smoothing : antialiased;
-moz-osx-font-smoothing : grayscale;
-webkit-overflow-scrolling : touch;
-webkit-tap-highlight-color : $body-bg;
-webkit-text-size-adjust : 100% }

body {
margin : 0;
padding : 0;
background-color : transparent;
@include ffoFamily();
font-weight : $font-weight;
@include font-size($font-size);
line-height : 1.6;
color : $text-color;

@include bp(bp4) {
@include font-size($font-size-plus-one); }

@include ffoFamily();
}

a, button, input, select, textarea, label, summary {
touch-action : manipulation; }

::-moz-selection {
background-color : $color-sky;
text-shadow : none; }

::selection {
background-color : $color-sky;
color: $white;
text-shadow : none; }

// midnight

body.midnight {
min-height : 100vh;
color : $white;
background-color : $color-midnight;

::-moz-selection {
background-color : lighten($color-midnight, 5%); }

::selection {
background-color : lighten($color-midnight, 5%); }

a,
a:visited {
color : inherit; }
}

.sr-only {
position: absolute;
width : 1px;
height : 1px;
margin : -1px;
padding : 0;
overflow: hidden;
clip: rect(0,0,0,0);
border-width : 0; }

.pull-left {
float : left;
margin-right : ($gutter / 2); }

.pull-right {
float : right;
margin-left : ($gutter / 2); }

// show-hide

[data-show-hide="container"] {
position : relative;
text-align : center; }

[data-show-hide="value"] {
html.js & {
visibility : hidden; }
}

[data-default="true"] {
html.js & {
visibility : visible; }
}

[data-show-hide="show-hide"] {
text-align : center; }
