img {
max-width : 100%;
border-width : 0;
vertical-align : middle;
-ms-interpolation-mode : bicubic; }

img[width],
img[height] {
max-width : none; }

.img--full {
width : 100%;
margin-bottom : ($vertical-spacing * 2); }

.img--right {
float : right;
margin-bottom : ($vertical-spacing / 4);
margin-left : ($gutter / 2);
width : auto; }

.img--left {
float : left;
width : auto;
margin-right : ($gutter / 2);
margin-bottom : ($vertical-spacing / 4); }

.img--center {
display : block;
width : auto;
margin-right : auto;
margin-bottom : ($vertical-spacing);
margin-left : auto; }

.img--right,
.img--left,
.img--center {

img {
width : auto; }
}

.img--bordered {
border-radius : 10px;
border : 5px solid $border-color; }

.img--circle {
border-radius : 50%; }

.icon--github {
background-image : url($root + 'img/icon-github.svg') !important; }

.icon--email {
background-image : url($root + 'icon-email.svg') !important; }

// figure

figure {
display : block;
margin : 0 0 ($vertical-spacing) 0;

img {
margin-bottom : ($vertical-spacing / 1.5); }
}

figcaption {
@include ffoFamily();
@include font-size($font-size-minus-three);
font-style : normal;
font-weight :normal;
text-align : left;
text-transform : none;

@include bp(bp4) {
@include font-size($font-size-minus-one); }
}

.figcaption__headline {
display : block;
margin-bottom : ($vertical-spacing / 4);
@include ffoFamily();
font-weight: $font-weight-bold;
@include font-size($font-size); }

.figure--full,
.main > figure {
margin : 0 0 ($vertical-spacing * 2) 0;

figcaption {
margin-left : 8.6745%;

@include bp(bp4) {
column-count : 2;
column-gap : ($gutter); }
}
}

.figure--above {
display : flex;
flex-direction : column-reverse; }

// icon

.icon {
position : relative;
overflow : hidden;
display : inline-block;
height : 30px;
width : 30px;
text-align : left;
text-indent : 100%;
font-size : 0;
background : transparent no-repeat 50% 50%;
background-size : 100% auto;

@include bp(bp4) {
height : 40px;
width : 40px; }

&:hover,
&:focus,
&:active {}
&:active {
transform : translate3d(1px, 1px, 0); }
}
