.list--in-page {
margin-bottom : ($vertical-spacing);
padding : ($vertical-spacing / 3) ($gutter / 2);
list-style-type : none;
background-color : $body-bg;
border-radius : $border-radius;

&:after {
display : table;
clear : both;
content :  ""; }

.active {
color : $breadcrumb-active-color; }

> li {
@include bp(bp4) {
float : left;
padding-left : 0;
text-indent : 0; }

&:before {
padding-right : 0;
content : ""; }
}

li + li::before {

@include bp(bp4) {
padding-right : ($gutter / 2);
padding-left : ($gutter / 2);
color : lighten($breadcrumb-active-color,20);
content : "|" }
}

li:before {
content : "\203a";
padding-right : ($gutter / 2);

@include bp(bp4) {
content : "";
padding-right : 0; }
}
}
