// 01 sg-page--index

.sg-page--index {
background : $brand-primary url($root + 'img/01-background.svg') no-repeat 0 0;
background-size : contain;

@include bp(bp6) {
background-size : cover; }

main {
text-align : center;

> a {
display : flex;
flex-direction : column;
align-items : center;
justify-content : center;
min-height : 100vh;
min-width : 100vw;
color : $white; }
}

h1, h2, p {
margin-bottom : 0; }

h1 {
@include font-size(54.93);

@include bp(bp4) {
@include font-size(68.66); }

@include bp(bp6) {
@include font-size(85.83); }
}

h2 {
@include font-size(14);

@include bp(bp4) {
@include font-size(18); }

@include bp(bp6) {
@include font-size(22.5); }

}

h2, p {
text-transform : uppercase; }

p {
@include font-size(14);

@include bp(bp4) {
@include font-size(18); }
}
}






// 02 sg-page--table-of-contents

.sg-page--table-of-contents {
background-image : url($root + 'img/02-background.svg');
background-position : 0 540px;
background-repeat : no-repeat; }

.sg-table-of-contents {
margin : 0 0 ($vertical-spacing) 0;
padding : 0 ($gutter) 0 0;

@include bp(bp3) {
margin : 0 0 ($vertical-spacing) 0; }

li {
display : block;
margin-bottom : ($vertical-spacing * 2);

> a {
display : flex;
align-items : center;

&:hover h3 {
color : $link-color; }
}
}

.sg-table-of-contents__content {
flex : 1; }

.sg-table-of-contents__number {
min-width : 8rem;
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size-plus-five);
line-height : 1;
text-align : right;
color : $link-color; }

h3 {
margin-bottom : 0;
@include font-size($font-size-plus-two);
text-transform : uppercase; }

p {
margin-bottom : 0;
@include font-size($font-size); }

body.midnight & {

a,
a:visited,
a:hover {
color : $white; }
}
}









// 03 tsg-page--getting-started

.sg-page--getting-started {
background-image : url($root + 'img/03-background.svg');
background-position : 0 0;
background-repeat : no-repeat; }









// 04 sg-page--design-principles

.sg-page--design-principles {
background-image : url($root + 'img/02-background.svg');
background-position : 0 540px;
background-repeat : no-repeat; }









// sg-alphabet

.sg-alphabet-letters {
@include font-size($font-size-plus-four);
letter-spacing : .2em;
line-height : 1.2;
word-wrap : break-word;

.active {
color : $brand-secondary;

body.midnight & {
color : $white; }
}

&[data-alphabet="sg-letters"] {
@include ffoFamily("Sunstroke Headline");
color : $link-color; }
}

.sg-alphabet-large {
@include ffoFamily("Sunstroke Headline");
@include font-size(240);
letter-spacing : .2em;
line-height : 1;
text-align : center;
vertical-align : middle;

@include bp(bp4) {
	@include font-size(320);
}
@include bp(bp5) {
	@include font-size(230);
}
@include bp(bp6) {
	@include font-size(300);
}
@include bp(bp7) {
	@include font-size(320);
}

body.midnight & {
color : $white; }
}








// sg-col__item

.sg-col__item {
margin-bottom : ($vertical-spacing * 4);

&:last-of-type {
margin-bottom : 0; }

h1, h2, h3, h4, h5, h6 {
margin-bottom : ($vertical-spacing / 4); }

.text-uppercase {
margin-bottom : ($vertical-spacing); }
}







// sg-colours

.sg-colours {
position : relative;
margin-bottom : 30px;

	.btn--small {
		@include bp(bp5) {
			@include font-size($font-size-minus-two);
		}
		@include bp(bp6) {
			@include font-size($font-size-minus-one);
		}
	}
}

.sg-colours__title {
text-align : center;
@include font-size($font-size); }

.sg-colours__list {
&,
li {
position : relative;
@include clearfix;
list-style-type : none;
margin : 0 0 10px;
padding : 0; }

li {
float : left;
width : 50%;
margin-left : -10px;
padding-left : 0;
@include font-size($font-size-minus-one);
text-align : right;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }

&:nth-child(2n) {
text-align: left;
margin-left: 10px;

.sg-colours__colour {
margin-left : 0;
margin-right : 5px; }
}

	&,
	&:nth-child(2n) {
		@include bp(bp5) {
			float: none;
			width: 110px;
			margin-left: auto; margin-right: auto;;
			text-align: left;
		}
		@include bp(bp7) {
			float: left;
			width: 50%;
			margin-left: -10px;
			text-align: right;

			&:nth-child(2n) {
				margin-left: 10px;
				text-align: left;
			}
		}
	}
}

.sg-colours__colour,
.sg-colours__hex {
display : inline-block;
vertical-align : middle; }

.sg-colours__colour {
height : 40px;
width : 40px;
margin-left : 5px;
border-radius : 100%; }
}









// sg-colour-circles

.sg-colour-circles {
list-style-type : none;
margin-left : 0;
padding : 0;

li {
display : inline-block;
margin-right : ($gutter / 4);
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }
}

span {
display : block;
@include font-size($font-size-minus-two);
text-align : center; }

img {
margin-bottom : ($vertical-spacing / 4);
width : 44px; }
}










// sg-help

.sg-help {
@include clearfix;
display : block;
margin : 0 0 ($vertical-spacing * 2);
background-color : #f2f2f4;
border : $border-width * 2 solid #f2f2f4;
border-radius : $button-border-radius; }

.sg-help__section__title {
html.js & {
display : none; }
}

.sg-help__section {
@include clearfix;
margin : 0 ($gutter);
padding : ($vertical-spacing / 2) 0;
color : $text-color;

&:first-child {
border-top-width : 0; }

p:last-child {
margin-bottom : 0; }

html.js & {
display : none;

&.active {
display : block; }
}
}

.sg-help__section__content {
@include font-size($font-size-minus-one);

p {
@include font-size($font-size-minus-one); }

.list--inline {
margin-bottom : 0;
@include font-size($font-size-minus-one); }
}

.sg-help__nav {
@include clearfix;
display : block;
margin : 3px;
border : 3px solid $white;
border-radius : ($button-border-radius / 2);

&[data-count="1"] {
.btn {
float : none;
display : block;
width : auto; }
}

&[data-count="2"] {
.btn {
width : 50%; }
}

&[data-count="3"] {
.btn {
width : 33.3333%; }
}

&[data-count="4"] {
.btn {
width : 25%; }
}

.btn {
overflow : hidden;
white-space : nowrap;
text-overflow : ellipsis;
box-sizing : border-box;
width : 33.3333%;
padding-top : 0.4rem;
border-right-width : 0;
@include font-size($font-size-minus-one);
text-align : center;
box-shadow : none;

&:after {
position: absolute;
top : 50%;
right : 8px;
content : "";
width : 0;
height : 0;
margin : -5px 0 0;
border-style : solid;
border-width : 10px 6px 0 6px;
border-color : $brand-primary transparent transparent transparent; }

&.active {
color : $color-charcoal;
background-image : linear-gradient(#e9e9e9, #fff);

&:after {
border-top-color : $brand-secondary; }
}

+ .btn {
margin: 0;
}

&:first-child {
border-top-left-radius : $button-border-radius / 2;
border-bottom-left-radius : $button-border-radius / 2; }

&:last-child {
border-right-width : $border-width;
border-top-right-radius : $button-border-radius / 2;
border-bottom-right-radius : $button-border-radius / 2; }
}
}


body.midnight {

.sg-help {
color : $white;
border-color : darken($color-midnight, 2%);
background-color : lighten($color-midnight, 5%); }

.sg-help__nav {
border-color : darken($color-midnight, 2%);

.btn {
color : $white;
border-color : $white;
background-color : lighten($color-midnight, 5%);

&:after {
border-top-color : $white; }

&.active {
color : $brand-secondary;

&:after {
border-top-color : $brand-secondary; }
}
}
}
}









// figures-flex

.sg-figures-flex {
margin : 0;
display : flex;

figure,
div {
flex : 1;
margin-right: ($gutter);
max-width : 160px; }

div img {
margin-bottom : ($vertical-spacing / 2); }
}

.sg-figures-flex--end {
@extend .sg-figures-flex;
align-items : flex-end; }








// list-icons

.sg-list-icons {
display : flex;
flex-wrap : wrap;

figure {
flex : 1 1 12%;
margin-right : ($gutter);
max-width : 12%; }

img {
margin-bottom : ($vertical-spacing / 8); }

figcaption {
text-align : center; }
}










// sg-content

body.sidebar--shown {
overflow : hidden;

@include bp(bp4) {
overflow : auto; }
}

.row--sky {
	background-color : $color-sky;
	margin-bottom : 4vh;
	margin-right : .5vh;
	padding : 4vh 2vw 2vh;
	color : white;
	border-radius : $border-radius *2;
}

.sg-content {
@include clearfix;

.sg-content__primary {
@include clearfix;
position : relative;
overflow : hidden;
float : none;
width : auto;
min-height : 1px;
margin : 0;
padding : 0;

&:after {
position : fixed;
top : 0;
bottom : 0;
left : 0;
right : 0;
z-index : 100;
display : none;
content : '';
background-color : rgba($color-midnight,.5);

body.sidebar--shown & {
display : block; }
}

@include bp(bp4) {
float : left;
width : 100%;

&:after {
&,
body.sidebar--shown & {
display : none; }
}
}

.sg-content__primary__inner {
margin : 0 ($gutter);
padding : ($vertical-spacing * 2) 0 0 ($gutter * 2.5);

@include bp(bp4) {
margin-left : 20rem; }

body.page--with-header & {
padding-top : 0; }
}

		.row {
			margin-bottom : ($gutter);
		}
}

.sg-content__secondary {
@include clearfix;
position : absolute;
top : 73px;
bottom : 0;
left : -24rem;
z-index : 200;
overflow: hidden;
width : 24rem;
margin : 0;
padding : 0;
background-color : $white;
transition : left 200ms cubic-bezier(0.23, 1, 0.32, 1) 0ms;

body.sidebar--shown & {
left : 0; }

.sg-content__secondary__inner {
overflow : auto;
height : 100%;
box-sizing : padding-box;
padding : ($vertical-spacing * 2) 0 0 ($gutter);

.sg-footer {
padding-bottom : ($vertical-spacing);

small {
display : block; }
}
}

body.midnight & {
color : $white;
background-color : $color-midnight;

@include bp(bp4) {
background-color : transparent; }
}

@include bp(bp4) {
position : relative;
top : auto;
bottom : auto;
left : auto;
float : left;
margin-left : -100%;
padding : 0;

.sg-content__secondary__inner {
padding : ($vertical-spacing * 2) 0 0 ($gutter); }
}
}
}









// sg-masthead

.sg-masthead {
margin-top : -($vertical-spacing * 2);
margin-bottom : ($vertical-spacing * 2);

[class*="col-"] {

@include bp(bp5) {
align-self : center; }
}

.sg-page--logos & {
min-height : 50vh;
background : transparent url($root + 'img/05-masthead@2x.png') repeat-x 50% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 455px;
background-size : 500px 454px; }
}

.sg-page--buttons & {
min-height : 50vh;
background : transparent url($root + 'img/12-masthead@2x.png') repeat-x 50% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 422px;
background-size : 1297px 422px; }
}

.sg-page--bubbles & {
min-height : 50vh;
background-color : transparent;
background-image : url($root + 'img/13-masthead-foreground@2x.png'), url($root + 'img/13-masthead-background@2x.png');
background-repeat : repeat-x, repeat-x;
background-position : 50% 0, -50% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 507px;
background-size : 1894px 507px; }
}

.sg-page--roundels & {
min-height : 50vh;
background : transparent url($root + 'img/14-masthead.svg') no-repeat 100% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 500px;
background-size : contain; }
}

.sg-page--icons & {
min-height : 50vh;
background : transparent url($root + 'img/15-masthead@2x.png') repeat-x -50% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 449px;
background-size : auto 449px; }

}

.sg-page--illustrations & {
min-height : 50vh;
background : transparent url($root + 'img/16-banner@2x.png') repeat-x 50% 0;
background-size : auto 50vh;

@include bp(bp4) {
min-height : 439px;
background-size : auto 439px; }

}
}







// sg-navigation

.sg-navigation {
ul, li {
list-style-type : none;
position : relative;
margin : 0;
padding : 0;
@include font-size($font-size-minus-one);

	a {
	padding : 0 0 0.5rem 0;
	}

}

ul {
margin-bottom : ($vertical-spacing);

&.nav.nav--intro {
li{
	&:first-of-type {
	@include font-size($font-size-plus-one);
	}
}

}

li {
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }
}

ul {
margin-left : ($gutter);
margin-bottom : 0;

//html.js & {
// display : none; } // Turning off temporarily while JS issue investigated
}
}

a {
display:  block; }
}

.midnight {
	.sg-navigation {
		a {
			&:hover {
				background-color: transparent;
			}
		}
	}
}







// sg-panels

.sg-panels {
margin-bottom : ($vertical-spacing / 2);
padding : ($gutter / 2) ($gutter / 2) 0 ($gutter / 2); }

.sg-panels__header {
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size);
text-transform : uppercase; }

.sg-panels__letter {
@include ffoFamily("Sunstroke Headline");
@include font-size(180);
line-height : 1;
text-align : right; }









// sg-scale

.sg-scale {
ul,
li {
position : relative;
@include clearfix;
list-style-type : none;
padding : 0;
margin : 0; }

.sg-scale__item {
display : inline-block;
width : 6.25rem;
padding : ($gutter / 4);
text-align : center;
vertical-align : bottom;
background-color : transparent;
border-radius : ($border-radius);

span,
strong {
display : block;
@include font-size($font-size-minus-one); }

&:hover {
background-color : $base-bg;

body.midnight & {
background-color : lighten($color-midnight, 5%); }
}
}
}








// misc

img[src$="hex.svg"] {
margin-bottom : ($vertical-spacing * 2); }
