a {
text-decoration : none;
color : $link-color;

&:visited {
color : $link-color-visited; }

&:hover {
color : $link-color-hover;
text-decoration : none; }

&:focus {
outline : thin dotted;
text-decoration : none;
color : $link-color-focus; }

&:hover,
&:active {
outline : 0; }
}

a:link {
transition : all $transition-duration $transition-timing-function; }

