.link--download {
@include ffoFamily();
font-weight : $font-weight-bold; }

[title|="Back to top"] {
display : inline-block;
padding : ($vertical-spacing / 2) ($gutter / 2); }

// In page navigation //

.box--midnight a[rel="jump-to"] {
  color: $color-sunshine;
}

a[rel="jump-to"]{
  font-weight: $font-weight;
  &:after {
      content:'';
      margin-left:.2rem;
      font-size:0.8em;
      vertical-align: bottom;
    }
}
