hr {
clear : both;
display : block;
margin : ($vertical-spacing * 2) 0;
padding : 0;
height : 2px;
border : 0;
border-bottom : 5px solid $hr-border; 

&.hr--dashed {
border-bottom-style : dashed; }

&.hr--dotted {
border-bottom-style : dotted; }

&.hr--blank {
margin : ($vertical-spacing) 0;
border-bottom-width : 0;

@include bp(bp5) {
margin : ($vertical-spacing * 2) 0; }
}
}
