.share {
display : inline-block;
list-style-type : none;
margin-bottom : ($vertical-spacing);
padding : ($gutter / 8) ($gutter / 4);
background-color : $base-bg;
border-radius : $border-radius;

li {
display : inline-block;
@include font-size($font-size-minus-one); 
padding-left : 0;
text-indent : 0; 

&:before {
padding-right : 0;
content : ""; }
}

img {
max-width : 28px; }

}