p {
margin-bottom : ($vertical-spacing);
line-height : $line-height-base;
@include font-size($font-size);

@include bp(bp5) {
@include font-size($font-size-plus-one); }
}

.introduction, .jumbotron .introduction {
@include font-size($font-size-plus-two);
line-height : ($line-height-base / 1.1);

@include bp(bp5) {
margin-bottom : ($vertical-spacing * 2);
@include font-size($font-size-plus-three);
line-height : ($line-height-base / 1.2); }
}

.lead {
@include font-size($font-size-plus-one);
line-height : ($line-height-base / 1.1);

@include bp(bp5) {
@include font-size($font-size-plus-two);
line-height : ($line-height-base / 1.2); }
}

.secondary {
@include font-size($font-size-minus-one);

@include bp(bp5) {
@include font-size($font-size); }
}

.tertiary {
@include font-size($font-size-minus-two);

@include bp(bp5) {
@include font-size($font-size-minus-one); }
}

.milli {
margin-bottom : ($vertical-spacing / 2);
@include font-size($font-size-minus-three);

@include bp(bp5) {
@include font-size($font-size-minus-two); }
}

.icon--info {
img {
float : left;
margin-right : ($gutter / 4);
width : 22px;}
}
