.accordion {
background-color : $accordion-menu-background;
margin : 0 0 ($vertical-spacing) 0;
padding: 0;

ul {
margin : 0;
padding : 0; }

li {
list-style-type : none;
margin-bottom : 0;
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }

> a {
display : block;
padding : $accordion-menu-list-padding;
color : $accordion-menu-color;
@include ffoFamily();
font-weight : $font-weight-bold;
text-decoration : none; }

&:focus,
&:hover {
background-color : $accordion-menu-hover; }
}

ul.submenu {
display : none;

li {
margin-bottom : 0;
background-color : $accordion-menu-sub-background;
border-bottom : $accordion-menu-border;

&:last-child {
border-width : 0; }

&:focus,
&:hover {
background-color : $accordion-menu-sub-hover; }
}

a {
color : inherit; }
}

.is-expanded {
display : block;
padding-bottom : 0; }

}
