// pager

.pager {
list-style-type : none;
margin-top : ($vertical-spacing);
margin-bottom : ($vertical-spacing);
padding-left : 0;
text-align : center; 
@include clearfix;

li {
display : inline; 
padding-left : 0;
text-indent : 0; 

&:before {
padding-right : 0;
content : ""; }
}
}

.pager-prev > a {
float : left; }

.pager-next > a {
float : right; }