// Misc

address {
margin-bottom : ($vertical-spacing);
@include font-size($font-size);
line-height : $line-height-base; }

abbr[title] {
border-bottom : 1px dotted $border-color;
cursor : help; }

dfn {
font-style : italic; }

ins {
background-color : $base-bg;
color : $text-color;
text-decoration : none; }

mark {
background-color : $base-bg;
color : $text-color;
font-style : italic;
font-weight : bold; }

// b, strong, i, em

b, strong {
@include ffoFamily();
font-weight : $font-weight-bold; }

i, em {
font-style : italic; }

// code

code {
vertical-align : middle;
background-color : transparent; }

.code-class {
font-weight : bold; }

pre {
white-space : pre;
white-space : pre-wrap;
word-wrap : break-word; }

// sub/sup

sub,
sup {
position : relative;
font-size: 60%;
line-height : 0; }

sup {
top : -.5em; }

sub {
bottom : -.25em; }

// small

small {
display : inline-block;
@include font-size($font-size-minus-one); }

// time

time {
@include font-size($font-size); }

// h-card

.h-card {
margin-bottom : ($vertical-spacing);
@include font-size($font-size); }

// text

.text-center {
text-align : center; }

.text-left  {
text-align : left; }

.text-right {
text-align : right; }

.text-justify  {
text-align : justify; }

.text-lowercase {
text-transform : lowercase; }

.text-uppercase {
text-transform : uppercase; }
