.content-inline {
@include clearfix; 

.content-inline__header,
.content-inline__content {
@include font-size($font-size); }

.content-inline__header {
float : left;
margin : 0 ($gutter / 4) 0 0;
line-height : 1.2; }

.content-inline__content {
margin-bottom : ($vertical-spacing / 2);
line-height : 1.3; }
}