// progress

.progress-units {
margin-bottom : ($vertical-spacing * 2);
padding-top : ($vertical-spacing);
position : relative;

@include bp(bp3) {
align-items : flex-start;
display : flex;
margin-bottom : ($vertical-spacing * 3);
margin-left : 0;
padding-top : 0; }

&:before {
background-color : $brand-neutral;
content : "";
height : calc(100% - 18px);
left : 28px;
position : absolute;
width : 3px;
z-index : -100;

@include bp(bp3) {
height : 3px;
left : 0;
top : 40px;
width : 100%; }
}
}

.progress__unit {
position : relative;
margin-bottom : 4px;

@include bp(bp3) {
flex : 1 1 20.3394%;
margin-bottom : 4px; }

&:last-child {
@include bp(bp3) {
margin-right : 0; }
}
}

.progress__header {
@include font-size($font-size);
line-height : 1;
padding-left : 44px;

@include bp(bp3) {
padding-left : 0;
text-align : center; }
}

.progress__icon {
position : absolute;
top : 0;
height : 11px;
width : 11px;
background-color : $white;
border : 5px solid $brand-neutral;
border-radius : 50%;

@include bp(bp3) {
top : 30px;
height : 22px;
width : 22px;
left : calc(50% - 11px); }
}

.progress__unit--active {

.progress__icon {
height : 11px;
width : 11px;
background-color : $brand-secondary;
border-color : $brand-secondary; 

@include bp(bp3) {
height : 22px;
width : 22px; }

}
}
