.sg-header {
@include clearfix;
position: relative; z-index : 300;
padding : 20px 20px 15px;
color : $white;
text-align : center;
background-color : $brand-primary;

&,
body.midnight & {
::-moz-selection {
background-color : $brand-primary; }

::selection {
background-color : $brand-primary; }
}

@include bp(bp4) {
text-align : left; }

.logo {
float : left;
margin : 0;
color: inherit;
@include font-size($font-size-plus-three);

a {
color : inherit; }

@include bp(bp4) {
width : 300 - 20px; }
}

.section {
display : none;
margin : 5px 0 0;
color: inherit;
@include ffoFamily("Sunstroke Headline");
font-weight: normal;
@include font-size($font-size);
text-transform : uppercase;
user-select : none;
cursor : default;

html.js & {
cursor : default; }

@include bp(bp2) {
display : inline-block; }

@include bp(bp4) {
margin-top : 0; }
}

.size {
float : right;
width : 25px;
height : 36px;
padding : 0 ($gutter) 0 30px;
@include ffoFamily("Sunstroke Headline");
font-weight: normal;
@include font-size($font-size-minus-one);
line-height: 36px;
background : transparent url($root + 'img/icon-sm.svg') no-repeat 0 50%;
background-size : 19px auto;

&:after {
display : inline-block;
content : "Sm"; }

@include bp(bp4) {
padding-left : 36px;
background-image : url($root + 'img/icon-md.svg');
background-size:  25px auto;

&:after {
content : "Md"; }
}

@include bp(bp7) {
background-image : url($root + 'img/icon-lg.svg');

&:after {
content : "Lg"; }
}
}

[data-toggle="navigation"] {
float : left;
width : 40px;
margin : 0 15px 0 0;
padding: 8px;
background-color : transparent;
border : none;
cursor : pointer;

@include bp(bp4) {
display : none; }

&:hover,
&:focus,
&:active {
opacity : .75; }

span {
overflow : hidden;
display : block;
height : 0;
margin-bottom : 4px;
background-color : $white;
border : 1px solid $white;

&:last-child {
margin-bottom : 0; }
}
}
}
