// contact-number

.c-contact-number {
margin-bottom : ($vertical-spacing);
line-height : 1.2; }

.c-contact-number__copy {
@include font-size($font-size); }

.c-contact-number__tel {
@include ffoFamily();
@include font-size($font-size-plus-two);
letter-spacing : .025em; }

.c-contact-number__time {
@include font-size($font-size-minus-one); }

.c-contact-number--large {

.c-contact-number__tel {
@include font-size($font-size-plus-three); }
}
