.packages {
margin-bottom : ($vertical-spacing);

@include bp(bp4) {
display : flex; }
}

.package {
box-sizing : border-box;
margin : 0 ($gutter / 8) ($vertical-spacing);
padding : ($vertical-spacing) ($gutter);
background-color : $base-bg;
border : $border-width normal $border-color;
border-radius : $border-radius;
text-align : center;

@include bp(bp4) {
display : flex;
flex-direction : column;
flex : 1;
transform : scale(1);
margin-bottom : 0;
padding : ($gutter / 2); }

&:last-of-type {

@include bp(bp4bp5) {
margin-right : 0; }
}


&:hover {
transition : transform $transition-duration $transition-timing-function;
transform : scale(1.05); }

li {
text-align : left; }

.btn {
margin-top : auto; }

}

.package__name {
margin-bottom : ($vertical-spacing / 2); }
