.media {
margin-bottom : ($vertical-spacing); 
display : flex;
align-items : flex-start; }

.box,
.panel {

.media {
margin-bottom : 0; }
}

.media__figure {
margin-bottom : ($vertical-spacing);
margin-right : ($gutter);
margin-bottom : 0; }

.media__content {
flex : 1; }

.media__header {
margin-bottom : 0; }

.media-body,
.media-body :last-child {
margin-bottom : 0; }

.media__title {
margin : 0 0 .5em; }

.media--center {
align-items : center; }


.media--reverse {
flex-direction : row-reverse; 

.media__figure {
margin-left : ($gutter); }
}

.media--center {
align-items : center; }