// curves

.curve {
padding : 6rem ($gutter) 2rem ($gutter);
background-color : $base-bg;
background-position : 0 0;
background-repeat : no-repeat;
background-size : contain;

@include bp(bp4) {
padding-top : 6rem; }

@include bp(bp5) {
padding-top : 10rem; }
}

.curve--left {
background-image : url($root + 'img/curve--left.svg'); } // TODO: Update reference

.curve--right {
background-image : url($root + 'img/site/curve--right.svg'); }

.curve--sky {
  @extend .curve;
  background-color : $color-sky;
  color : $white;

  a {

    // Prevents plain text link styles in Sky boxes being inherited by buttons //
    &:not(.btn) {
      color : $white;
      text-decoration: underline;}
  }
}

.curve--sky-left {
background-image : url($root + 'img/site/curve-blue-left-Artboard.svg'); }

.curve--sky-right {
background-image : url($root + 'img/site/curve--sky-right.svg'); }
