@media print {
    #cookie-bar {
        display: none
    }
}

#cookie-bar {
    background: #231f20;
    line-height: 24px;
    color: #eee;
    text-align: center;
    padding: 18px 0;
    z-index: 4;
    position: relative;
    .fixed {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
    }
    .cookie-bar-container {
        max-width: 1006px;
        margin: 0 auto;
    }
    p {
        margin: 0;
        padding: 0;
        text-align: left;
        font-size: 13px
    }
    a {
        color: #fff;
        display: inline-block;
        border-radius: 3px;
        text-decoration: none;
        padding-left: 4px
    }
    .cb-enable {
        background: #d6146d;
        display: inline-block;
        float: right;
        margin-right: 0;
        padding: 5px 10px;
        border-radius: 3px;
        line-height: 1.5;
        font-size: 15px;
        &:hover {
            background: #d6146d;
        }
    }
    .cb-disable {
        background: #fda829;
        &:hover {
            background: #b00;
        }
    }
    .cb-policy {
        text-decoration: underline
    }
}
