.form-group {
    position: relative;

    @include bp(bp4) {
        display: flex;
        margin-bottom: ($vertical-spacing);
    }

    input,
    textarea {

        @include bp(bp4) {
            flex: 2;
            margin: 0;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            border-left-width: 0;
        }
    }
    label {

        @include bp(bp4) {
            flex: 1;
            margin: 0;
            padding: $input-base-vertical $input-base-horizontal;
            background-color: $brand-primary;
            border-top-left-radius: $border-radius;
            border-bottom-left-radius: $border-radius;
            color: $white;
            text-indent: 0;
        }
    }
    .select {
        width: 100%;
        flex: 2;
        padding: 0 1.25rem;
        background-color: #fff;
        border: 2px solid #b2a9a3;
        border-radius: $border-radius;
        background-color: white;
        padding-left: 0;
        padding-right: 28px;


        /* APPIUS: Specific styles for the select input element */
        @media (min-width: 48em) {
            margin-bottom: 0;
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
            border-left-width: 0;
        }
        > select {
            background-color: white;
            margin: 0;
            border: 0;
            color: inherit;
            width: 100%;
            display: block;
        }
        &__arrow {
            border-color: $color-sky rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
            border-width: 13px 8px 0 8px;
            right: 1.5vw;
            @media (max-width: 47em) { // Mobile only
                right: 3vw;
                top: 4.7rem
            }

        }
    }
}

.has-error {

    label {

        @include bp(bp4) {
            background-color: $color-ketchup;
        }
    }

    input,
    textarea,
    select {
        border-color: $color-ketchup;
    }

    .select__arrow {
        border-color: $color-ketchup rgba(0, 0, 0, 0) rgba(0, 0, 0, 0) rgba(0, 0, 0, 0);
    }
}

.form-radiobutton > label.control--radio:first-child { padding-left: 0; }

/* APPIUS: Resetting button's text alignment */
a.btn { text-align: center; }
