.pagination {
margin-left : 0;
padding : 0;

li {
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }
}
}

.page-item {
display : inline-block;
box-sizing : border-box;
margin-right : 2px; }

.page-link {
display : inline-block;
padding : .6rem 1.6rem .4rem;
background-color : $pagination-background;
border : $pagination-border;
border-radius : $border-radius;
@include ffoFamily("Sunstroke Headline");
font-weight : 500;
@include font-size($font-size);
color : $pagination-color;
text-transform : none;

&:hover {
background-color : $pagination-background-hover;
color : $pagination-color-hover; }
}

.active .page-link {
background-color : $pagination-background-active;
color : $pagination-color-active;
cursor : default; }

.pagination-sm {

.page-link {
padding : $pagination-small-padding;
@include font-size($font-size-minus-one);
border-radius : $pagination-small-border-radius; }
}

.pagination-lg {

.page-link {
padding : $pagination-large-padding;
border-radius : $pagination-large-border-radius; }
}
