// bubble

.bubble {
position : relative;
padding : 1rem 1.25rem;
border-radius : ($border-radius * 3);
@include ffoFamily();
font-weight : $font-weight-bold;
line-height : 1.2;
text-align : center; }

.bubble--bottom {
margin : ($vertical-spacing) 0 32px 0;

&:after {
content : '';
position : absolute;
bottom : -22px;
left : calc(50% - 14px);
display : block;
width : 27px;
height : 22px; }
}

.bubble--top {
margin : 32px 0 ($vertical-spacing);

&:before {
content : '';
position : absolute;
top : -22px;
left : calc(50% - 14px);
display : block;
width : 27px;
height : 22px; }
}

.bubble--midnight {
background-color : $color-midnight;
color : $color-sunshine;

b {
color : $color-sunshine; }

&:before {
background : transparent url($root + 'img/bubble-tip-up-midnight.svg') no-repeat 0 0; }

&:after {
background : transparent url($root + 'img/13-bubble-tip-down-midnight.svg') no-repeat 0 0; }
}

.bubble--ketchup {
background-color : $color-ketchup;
color : $white;

&:before {
background : transparent url($root + 'img/bubble-tip-up-ketchup.svg') no-repeat 0 0; }

&:after {
background : transparent url($root + 'img/bubble-tip-down-ketchup.svg') no-repeat 0 0; }
}

.bubble--sunshine {
background-color : $color-sunshine;
color : $color-midnight;

b {
color : $color-ketchup; }

&:before {
background : transparent url($root + 'img/bubble-tip-up-sunshine.svg') no-repeat 0 0; }

&:after {
background : transparent url($root + 'img/bubble-tip-down-sunshine.svg') no-repeat 0 0; }
}

.bubble--white {
background-color : $white;
color : $color-sky;

b {
color : $color-ketchup; }

&:before {
background : transparent url($root + 'img/bubble-tip-up-white.svg') no-repeat 0 0; }

&:after {
background : transparent url($root + 'img/bubble-tip-down-white.svg') no-repeat 0 0; }
}
