/* APPIUS: This whole file is brand new */
.primary-nav {
		background-color: $color-sky;
		position: relative;
		padding-top: 5px;
		padding-bottom: 0;
		min-height: 60px;
	> .container {
		position: relative;
		@media (max-width: $screen-sm-max) {
			padding-left: 0;
			padding-right: 0;
		}
	}
	&__upper {
		float: right;
		display: none;
		@media (min-width: $screen-sm-min) {
			display: inline-block;
			position: absolute;
			top: 0;
			right: 0;
		}
	}

	&__footer {
		background-color: $color-midnight;
		display: block;
		font-size: 30px;
		text-align: center;
		.primary-nav__tertiary-links {
            display: block;
        }
		@media (min-width: $screen-sm-min) {
			display: none;
		}
	}
	&__support-text, &__tertiary-links, &__items, &__logo {

		color: white;
		@media (min-width: $screen-sm-min) {
			display: inline-block;
		}
	}

	&__support-text {
		color: white;
		font-size: 19px;
		@include ffoFamily("Sunstroke Headline");
		font-weight: normal;
		@media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
			font-size: 20px;
			margin-right: 20px;
		}
		@media (min-width: $screen-lg-min) {
			font-size: 22px;
			margin-right: 20px;
		}
		.tel {
			font-size: 1.28em;
			a { // iOS converts any numbers into an anchor tag automatically
				color: white;
			}

			@media (min-width: $screen-sm-min) {
				font-size: 1.38em;
			}
		}
	}

	&__tertiary-links {
		color: white;
		display: none;
		@media (min-width: $screen-sm-min) {
			display: inline-block;
		}
		> ul {
			margin-bottom: 0;
			li { margin-right: 26px; }
		}
		a, a:link {
			font-size: 22px;
			color: white;
			@include ffoFamily("Sunstroke Headline");
			font-weight: normal;
			@media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
				font-size: 20px;
			}
		}
	}

	&__items {
		color: white;
		margin-top: 20px;
		padding: 0 15px;
		padding-right: 3px;
		@media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
			padding-left: 0;
			padding-right: 0;
			display: block;
			margin-top: 73px;
		}

		@media (min-width: $screen-lg-min) {
			margin-top: 60px;
			padding: 0 15px;

		}
		&.collapse {
			display: none;
			&.in { display: block;}
		}
		&.panel-open {
			.primary-nav__top-level {
				@media (min-width: $screen-xs-max) {
					opacity: .4;
				}
			}
		}
		.mobile-only {
			@media (min-width: $screen-sm-min) {
                display: none;
            }
            .primary-nav__top-level {  @include ffoFamily("Sunstroke Body"); }
        }
		> li {
			margin-right: 22px;
			padding: 0;
			list-style: none;
			text-indent: 0;
			padding-left: 0;
			@media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
				margin-right: 12px;
			}
			&:before {
				display: none;
			}

			@media (min-width: $screen-sm-min) {
				display: inline-block;
			}
		}
	}

	&__top-level {
		.primary-nav .dropdown.is-open & {
			opacity: 1;
			&:after {
				content: '\2227';
				color: $color-lime
			}
		}

		@media (max-width: $screen-xs-max) {
			width: 100%;
			display: block;
			padding: 4px 15px;
		}
	}

	&__top-level,
	&__top-level:link,
	&__top-level:visited,
	&__top-level:hover {
		font-size: 23px;
		color: white;
		@include ffoFamily("Sunstroke Headline");
		font-weight: normal;
		@media (min-width: $screen-sm-min) and (max-width: $screen-md-max) {
			font-size: 20px;
		}
	}

	&__logo {
		margin-top: 2px;
		max-width: 90px;
    	margin-left: 15px;
		@media (min-width: $screen-sm-min) {
			max-width: 113px;
			float: left;
			margin-right: 10px;
			margin-top: 10px;
			margin-left: 0;
		}

		@media (min-width: $screen-lg-min) {
			max-width: 150px;
			margin-top: 17px;
		}
	}

	&__container {
		&.in { overflow-y: auto; }
		&.collapse {
			display: none;
			&.in { display: block; }
			@media (min-width: $screen-sm-min) {
				display: block;
				height: auto !important;
				overflow: visible;
				width: auto;
			}
		}
	}
	.collapsing {
	    height: 0;
	    overflow: hidden;
	    transition-property: height,visibility;
	    transition-duration: .35s;
	    transition-timing-function: ease
	}
	.menu-btn {
		position: absolute;
		top: 4px;
		right: 15px;
		cursor: pointer;
		display: block;
		@media (min-width: $screen-sm-min) {
			display: none;
		}
		&__text {
			vertical-align: middle;	;
			float: left;
			margin-right: 10px;
			margin-top: 7px;
			color: white;
			@include ffoFamily("Sunstroke Headline");
			font-weight: normal;
		}

		&.collapsed {
			.nav-mobile {
				background-color: white;
				&:before {
					top: -6px;
					transform        : rotate(0);
					-webkit-transform: rotate(0);
					-moz-transform   : rotate(0);
					-o-transform     : rotate(0);
					-ms-transform    : rotate(0);
				}

				&:after {
					bottom: -6px;
					transform        : rotate(0);
					-webkit-transform: rotate(0);
					-moz-transform   : rotate(0);
					-o-transform     : rotate(0);
					-ms-transform    : rotate(0);
				}
			}
		}
	}
	&__toggle {
		position: relative;
		float: none;
		margin: 0;
		padding: 0;
		height: 35px;
		width: 30px;
		background-color: rgba(0, 0, 0, 0);
		border: 1px solid rgba(0, 0, 0, 0);
		border-radius: 4px;
		-webkit-appearance: none;

		.nav-mobile,
		.nav-mobile:after,
		.nav-mobile:before {
			transition: all .3s ease-in-out;
			cursor: pointer;
			height: 2px;
			width: 25px;
			background-color: white;
			position: absolute;
			display: block;
			content: '';
		}

		.nav-mobile { background-color: transparent; }
		.nav-mobile::before {
			top: 0;
			transform        : rotate(45deg);
			-webkit-transform: rotate(45deg);
			-moz-transform   : rotate(45deg);
			-o-transform     : rotate(45deg);
			-ms-transform    : rotate(45deg);
		}

		.nav-mobile::after {
			bottom: 0;
			transform        : rotate(-45deg);
			-webkit-transform: rotate(-45deg);
			-moz-transform   : rotate(-45deg);
			-o-transform     : rotate(-45deg);
			-ms-transform    : rotate(-45deg);
		}

		.icon-bar-text {
			color: white;
			font-size: 12px;
			display: block;
			background-color: rgba(0, 0, 0, 0);
			position: absolute;
			bottom: 0;
			width: 25px;
			text-align: center;
		}
	}

	.dropdown {
		a.primary-nav__top-level {
			position: relative;
			padding-right: 20px;
            padding-left: 15px;
			&:after {
				content: '\2228';
				color: #fff;
				display: block;
				position: absolute;
				right: 0;
				top: 0;
				font-size: 22px;
				font-weight: bold;
				line-height: 2;
                @media (min-width: $screen-sm-min) {
				    line-height: 1.3;
                }
			}
		}
	}

	&__panel {
		background-color: white;
		left: 0;
		margin-left: -15px;
		margin-right: -25px;
		transition: height 0.5s ease-in-out;
        overflow: hidden;
        display: none;

		@media (max-width: $screen-xs-max) {
			display: block;
			.dropdown:not(.is-open) & {
				height: 0px !important;
			}
		}

		.dropdown.is-open & {
			@media (min-width: $screen-sm-min) {
				display: block;
			}
		}

		@media (min-width: $screen-sm-min) {
            height: auto !important;
			display: none;
			border: 6px solid $color-lime;
			border-radius: 12px;
			padding: 36px 25px;
			position: absolute;
			top: 130px;
			left: 0;
			right: 0;
			margin: 0;
			z-index: 500;
		}

		@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
			padding: 15px 5px;

		}
        .primary-nav__details:hover {
            .primary-nav__link-text {
                color: $color-lime;
            }
        }

        .primary-nav__link-text {
            &:hover {
                color: $color-lime;
            }
        }
	}

	&__column {
		vertical-align: top;
		display: block;
		width: 100%;
		border: none;
		padding: 15px 10px;
		@media (max-width: $screen-xs-max) {
			padding-bottom: 0;
		}
		@media (min-width: $screen-sm-min) {
			display: inline-block;
			width: 33%;
			padding: 0 15px;
		}

		@media (min-width: $screen-md-min) {
			padding: 0 20px;
		}

		&--border-right {
			border-bottom: 2px solid #e1d6da;
			@media (min-width: $screen-sm-min) {
				border-bottom: none;
				border-right: 2px solid #e1d6da;
			}
		}
	}

	&__item {
		width: 100%;
		margin-bottom: 20px;
        @include clearfix();
		&--is-article {
            .primary-nav__image {
                max-width: 120px;
                border-radius: 6px;

				@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
					float: none;
					display: block;
					margin-bottom: 5px;
					max-width: 100%;
				}
            }

			.primary-nav__details {
				@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
					padding-left: 0;
				}
			}
			@media (max-width: $screen-xs-max) {
				.primary-nav__image {
					display: none;
				}
			}
		}

		&--is-icon {
            .primary-nav__image {
                max-width: 44px;
            }
		}

		&--no-description.primary-nav__item--is-icon .primary-nav__link {
			position: relative;
			top: 10px;
		}
	}

	&__details {
		padding-left: 65px;
	}

	&__link {
		&:hover {
			&-text {
				color: $color-lime;
			}
		}
		&-text {
			color: $color-sky;
            font-weight: normal;
            font-size: 20px;
            line-height: 1.1;
			@include ffoFamily("Sunstroke Headline");
			font-weight: normal;;
            display: block;
		}
	}
	&__image {
		float: left;
		margin-right: 20px;
	}
	&__description {
		color: $color-midnight;
		font-weight: normal;
		font-size: 16px;
		margin-bottom: 0;
		margin-top: 2px;

		@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) {
			font-size: 15px;
		}
	}

	&__items li.dropdown,
	&__items li.no-panel, {
		&:first-child {
			.primary-nav__top-level {
	   			@media (min-width: $screen-sm-min) {
	   				padding-left: 0 !important;
	   			}
	   		}
		}

		.primary-nav__top-level {
			@media (max-width: $screen-xs-max) {
				padding-left: 0 !important;
			}
		}
	}
}
