h1, h2, h3, h4, h5, h6 {
margin-top : 0;
font-weight: $font-weight;
}

h1,
.h1, .jumbotron h1 {
margin-bottom : ($vertical-spacing / 2);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size-plus-four);
line-height : $headings-line-height;

@include bp(bp4) {
@include font-size($font-size-plus-five); }

@include bp(bp6) {
margin-bottom : ($vertical-spacing * 2);
@include font-size($font-size-plus-six); }

small {
@include font-size($font-size-plus-two); }
}

h2,
.h2 {
margin-bottom : ($vertical-spacing / 2);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size-plus-three);
line-height : $headings-line-height;

@include bp(bp4) {
@include font-size($font-size-plus-four); }

@include bp(bp6) {
margin-bottom : ($vertical-spacing * 2);
@include font-size($font-size-plus-five); }

small {
@include font-size($font-size-plus-two); }
}

p + h2 {
margin-top : ($vertical-spacing / 1.5); }

h3,
.h3 {
margin-bottom : ($vertical-spacing / 2);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size-plus-two);
line-height : $headings-line-height;

@include bp(bp4) {
@include font-size($font-size-plus-three); }

@include bp(bp6) {
margin-bottom : ($vertical-spacing);
@include font-size($font-size-plus-four); }
}

h4,
.h4 {
margin-bottom : ($vertical-spacing);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size-plus-one);
line-height : $headings-line-height;

@include bp(bp4) {
@include font-size($font-size-plus-two); }

@include bp(bp6) {
margin-bottom : ($vertical-spacing);
@include font-size($font-size-plus-three); }
}

h5,
.h5 {
margin-bottom : ($vertical-spacing);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size);
line-height : $headings-line-height;

@include bp(bp4) {
@include font-size($font-size-plus-one); }

@include bp(bp6) {
@include font-size($font-size-plus-two); }
}

h6,
.h6 {
margin-bottom : ($vertical-spacing);
@include ffoFamily("Sunstroke Headline");
@include font-size($font-size);
line-height : $headings-line-height; }

// heading--icon

.heading--icon {
margin-bottom : ($vertical-spacing);

> a {
display : flex;
align-items : center;
padding-bottom : ($vertical-spacing);  }

img {
margin-right : ($gutter); }

}
