.accordion-tabs-minimal {
margin-bottom : ($vertical-spacing);

@include clearfix;
line-height : 1.5;
padding : 0;

.tab-header-and-content {
list-style-type : none;
margin : 0;
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }

@include bp(bp4) {
display : inline; }
}

.tab-link {
display : block;
padding : ($vertical-spacing / 2) ($gutter);
background-color : $tab-inactive-color;
border-top : $border-width solid $border-color;
color : $link-color;
@include ffoFamily();
font-weight : $font-weight-bold;
text-decoration : none;
margin-left: 3rem;

@include bp(bp4) {
display : inline-block;
border-top-width : 0; }

&:hover {
color : $link-color-hover; }

&:focus {
outline : none; }

&.is-active {
border-bottom : 0;
color : $text-color;

@include bp(bp4) {
border : $border-width solid $brand-neutral;
border-bottom: ($body-bg solid $border-width * 2);
border-top-right-radius : $border-radius;
border-top-left-radius : $border-radius;
margin-bottom : -3px; }
}
}

.tab-content {
display : none;
padding : ($vertical-spacing) ($gutter) 0;
width : 100%;

@include bp(bp4) {
float : left;
border-top : $border-width solid $brand-neutral; }
}
}
