.main  {
    form {
        margin: 0 0 ($vertical-spacing) 0;
        @include ffoFamily();
        color: $color-charcoal;

        p {
            margin-bottom: ($vertical-spacing / 2);
        }

        ul {
            list-style-type: none;
            margin: 0 0 ($vertical-spacing) 0;
            padding: 0;
        }

        li {
            margin-bottom: 0;
        }
    }

    legend {
        display: block;
        width: 100%;
        margin-bottom: ($vertical-spacing * 2);
        padding: 0 0 ($vertical-spacing / 2) 0;
        @include font-size($font-size);
        line-height: inherit;
        color: $color-charcoal;
        border-width: 0;
        border-bottom: $border-width solid $input-border;
    }

    fieldset {
        margin-bottom: 0;
        padding: 0;
        border-width: 0;
        min-width: 0;
    }

    label {
        display: block;
        max-width: 100%;
        margin-bottom: 0;
        @include ffoFamily("Sunstroke Headline");
        font-weight: normal;
        @include font-size($font-size);
        color: $text-color;

        span {
            @include font-size($font-size-minus-one);
        }
    }
}

.bizform {
    .introduction { margin-top: 1.5rem; }
}
