// alert

.alert {
margin-bottom : ($vertical-spacing);
padding : $alert-padding; 
border-radius : $alert-border-radius;

p {
margin-bottom : 0; }

a {
color : inherit;
text-decoration : underline; }
}

.alert--lime {
background-color : $color-lime;
color : $white; }

.alert--midnight {
background-color : $color-midnight;
color : $white; }

.alert--ketchup {
background-color : $color-ketchup;
color : $white; }