button {

&:focus,
&:active {
outline : none;
box-shadow : none;
border : none; }
}

// btn

.btn {
-webkit-user-select : none;
-moz-user-select : none;
-ms-user-select : none;
user-select : none;
display : inline-block;
padding : $button-padding;
border-radius : $button-border-radius;
@include ffoFamily();
@include font-size($button-font-size);
font-weight : $button-font-weight;
letter-spacing : $button-letter-spacing;
line-height : 1.5em;
text-decoration : none;
text-transform : $button-font-transform;
cursor : pointer;
transition : all $transition-duration $transition-timing-function;
white-space: normal;

// theme

background-color : $brand-primary;
background-image : linear-gradient(#1aa0e0, #1b87b6);
border : $border-width $button-border-style #47b9e9;
box-shadow : 0 0 0 2px #17769e, 0 0 0 4px $btn-primary-border;
text-shadow : 0 2px 0 #216c8b;
color : $white;

&:visited {
background-color : $brand-primary;
background-image : linear-gradient(#1aa0e0, #1b87b6);
border : $border-width $button-border-style $btn-primary-border;
box-shadow : 0 0 0 2px #17769e, 0 0 0 4px #e4e9ec;
text-shadow : 0 2px 0 #216c8b;
color : $white; }

&:hover,
&:focus,
&:active {
background-image : linear-gradient(#1b87b6, #1aa0e0);
text-shadow : 0 -2px 0 #216c8b;
color : $white; }
}

// button--primary

.btn--primary {
background-color : $btn-success-bg;
background-image : linear-gradient($btn-success-bg, #738f22);
border-color : #a5c155;
box-shadow : 0 0 0 2px #607328, 0 0 0 4px rgba(255,255,255,0.5);
text-shadow : 0 2px 0 #607328;
color : $white;

&:visited {
background-color : $btn-success-bg;
background-image : linear-gradient($btn-success-bg, #738f22);
border-color : #a5c155;
box-shadow : 0 0 0 2px #607328, 0 0 0 4px rgba(255,255,255,0.5);
text-shadow : 0 2px 0 #607328;
color : $white; }

&:hover,
&:focus,
&:active {
background-image : linear-gradient(#738f22, $btn-success-bg);
text-shadow : 0 -2px 0 #607328;
color : $white; }
}

// button--ketchup

.btn--ketchup {
background-color : $btn-warning-color;
background-image : linear-gradient($btn-warning-color, #da241e);
border-color : #fb5555;
box-shadow : 0 0 0 2px #e02628, 0 0 0 4px #e4e9ec;
text-shadow : 0 2px 0 #b72b29;
color : $white;

&:visited {
background-color : $btn-warning-color;
background-image : linear-gradient($btn-warning-color, #da241e);
border-color : #fb5555;
box-shadow : 0 0 0 2px #e02628, 0 0 0 4px #e4e9ec;
text-shadow : 0 2px 0 #b72b29;
color : $white; }

&:hover,
&:focus,
&:active {
background-image : linear-gradient(#da241e, $btn-warning-color);
text-shadow : 0 -2px 0 #b72b29;
color : $white; }
}

// button--link

.btn--link {
background-color : $white;
background-image : linear-gradient($white, #e9e9e9);
border-color : #009fe3;
box-shadow : 0 0 0 2px #f2f2f4;
text-shadow : none;
color : #009fe3;

&:visited {
background-color : $white;
background-image : linear-gradient($white, #e9e9e9);
border-color : #009fe3;
box-shadow : 0 0 0 2px #f2f2f4;
text-shadow : none;
color : #009fe3; }

&:hover,
&:focus,
&:active {
background-image : linear-gradient(#e9e9e9, $white);
text-shadow : none;
color : #009fe3; }
}

// small

.btn--small {
padding : $button-small-padding;
@include font-size($button-small-font-size);
border-radius : $btn-border-radius-small; }

// large

.btn--large {
padding : $button-large-padding;
@include font-size($button-large-font-size); }

// block

.btn--block {
display : block;
padding-right : 0;
padding-left : 0;
width : 100%;
text-align : center; }

// btn-group

.btn-group {
position : relative;
display : inline-block;
margin-bottom : ($vertical-spacing);
vertical-align : middle;

.btn {
position: relative;
float : left;
border-radius : 0;
border-right-width : 0;

&:first-child {
border-top-left-radius : $btn-border-radius-base;
border-bottom-left-radius : $btn-border-radius-base; }

&:last-child {
border-right-width : $border-width;
border-top-right-radius : $btn-border-radius-base;
border-bottom-right-radius : $btn-border-radius-base; }
}

.btn + .btn {
margin-left : -($border-width); }
}


.form-group .btn { margin-bottom: 10px; }
