.main {
    ul, ol {
        margin: 0 0 ($vertical-spacing) ($gutter);
        padding: 0 0 0 ($gutter);

        @include bp(bp3) {
            margin: 0 0 ($vertical-spacing) 0;
        }

        li {
            margin-bottom: ($vertical-spacing / 8);
            @include font-size($font-size);
        }
    }

    ul {
        list-style-type: none;
        margin-left: 0;
        padding-left: 0;

        li {
            &:before {
                padding-right: ($gutter / 4);
                content: "\2022";
                color: $brand-supporting;
            }
        }
    }
}

dt {
    margin-bottom: ($vertical-spacing / 8);
    @include ffoFamily("Sunstroke Headline");
    @include font-size($font-size-plus-two);
}

dd {
    margin-bottom: ($vertical-spacing * 2);
    margin-left: 0;
    @include font-size($font-size);

    @include bp(bp5) {
        @include font-size($font-size-plus-one);
    }
}

// list--footnotes

.list--footnotes {

    li {
        margin-bottom: ($vertical-spacing / 2);
        @include font-size($font-size-minus-two);

        @include bp(bp4) {
            @include font-size($font-size-minus-one);
            line-height: 1.4;
        }
    }

    li > ul,
    li > ol {
        list-style-type: square;
        margin: ($vertical-spacing) 0 ($vertical-spacing * 2) -22px;
        padding: 0 0 0 ($gutter);
    }
}

li > ol {
margin : ($vertical-spacing) 0 ($vertical-spacing * 2) 0;
}

// list--icon

.list--icon {
list-style-type : none;
margin-left : 0;
padding-left : 0;

li {
flex:0 0 auto;
padding-left : 3.5rem;
margin-bottom : ($vertical-spacing);
text-indent : 0;
position:relative;

@include bp(bp5){
padding-left : 4.5rem;
}

&:before {
padding-right : 0;
content : ""; }
}

img {
margin-right : ($gutter / 2);
width : 22px;
position:absolute;
left:0;

 //@include bp(bp5) {
//width : 33px; }
  }
}

// list--inline

.list--inline,
.list-inline {
list-style-type : none;
margin-left : 0;
padding-left : 0;

li,
.list-inline-item {
display : inline-block;
margin-right : ($gutter);
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }

&:last-child {
margin-right : 0; }
}
}

// list--plain

.list--plain,
.list-unstyled {
list-style-type : none;
margin-left : 0;
padding-left : 0;

li {
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }
}
}

// list--nested

.list--nested {
list-style-type : none;

> li {
padding-left : 0;
text-indent : 0;

&:before {
padding-right : 0;
content : ""; }
}

li > ul,
li > ol {
list-style-type : none;
margin : 0 0 0 $gutter;
padding : 0; }
}
