table {
width               : 100%;
max-width           : 100%;
margin-bottom       : ($vertical-spacing);
border-collapse     : collapse;
border              : none;
border-collapse     : separate;
border-spacing      : 3px;
@include ffoFamily(); }

th,
td {
padding             : $table-padding;
//background-color    : $table-bg;
@include font-size($table-font-size);
line-height         : ($line-height-base / 1.5);
text-align          : left;
vertical-align      : center;
}

th {
font-weight         : normal;
text-align          : left;
@include ffoFamily("Sunstroke Headline"); }

thead,
tfoot {

th,
td {
background-color    : $table-header-bg;
text-transform      : uppercase; }
}

tr                  :nth-of-type(even) th,
tr                  :nth-of-type(even) td {
background-color    : $table-stripe-bg; }

th p {
@include ffoFamily(); }

// table--condensed

.table--condensed {

th,
td {
padding             : $table-padding / 2;
@include font-size($table-condensed-font-size); }
}

// table--striped

.table--striped {
border-collapse     : collapse;
border-spacing      : 0;

tbody {

th,
td {
// background-color : $white;
border-top          : $border-width solid $table-border-color; }

tr                  :nth-child(odd) td,
tr                  :nth-child(odd) th {
background-color    : $table-stripe-bg;
color               : $table-stripe-font-color; }
}
}

// table--midnight

.table--midnight {

thead,
tfoot {

th,
td {
background-color    : $color-midnight;
color               : $white; }
}
}

.table__clear {
background-color : transparent!important; }
