.comparison {
display : flex;
margin-bottom : ($vertical-spacing); }

.comparison__item {
flex : 1;
margin-right : 1px;
padding : ($vertical-spacing) ($gutter / 2);
background-color : $color-midnight;
color : $white;
line-height : 1.2;
text-align : center;

&:hover {
background-color : $color-lime; }

&:first-of-type {
border-top-left-radius : $border-radius;
border-bottom-left-radius : $border-radius; }

&:last-of-type {
border-top-right-radius : $border-radius;
border-bottom-right-radius : $border-radius; }
}

.comparison__price {
font-weight : $font-weight-bold;
@include ffoFamily();
@include font-size($font-size-plus-four); }

.comparison__currency {
@include font-size($font-size-plus-one); }
