// base

.base {
margin-bottom : ($vertical-spacing);
padding : $panel-padding;
background-color : $base-bg;

p:last-child,
p:only-child,
ul:last-child,
ul:only-child,
ol:last-child,
ol:only-child {
margin-bottom : 0; }
}

// box

.box {
margin-bottom : ($vertical-spacing);
background-color : $panel-bg;
border : $panel-border-width solid $panel-border-color;
border-radius: $panel-border-radius;
box-shadow : $panel-box-shadow;

p:last-child,
p:only-child,
ul:last-child,
ul:only-child,
ol:last-child,
ol:only-child,
table {
margin-bottom : 0; }

.heading--icon a {
padding-bottom : 0; }
}

.box__body {
padding : $panel-padding;
@include clearfix; }

.box__cols {
padding : ($vertical-spacing) ($gutter); }

.box__title {
margin-top : ($vertical-spacing * 1.5);
margin-bottom : 0; }

.box__content {
@include font-size($font-size); }

.box__heading {
padding : $panel-padding;
background-color : $panel-heading-bg;
border-bottom : $panel-border-width solid $panel-heading-border;
border-top-left-radius : inherit;
border-top-right-radius : inherit;
color : $panel-heading-color; }

.box__footer {
padding : $panel-padding;
background-color : $panel-footer-bg;
border-top : $panel-border-width solid $panel-footer-border;
border-bottom-left-radius : inherit;
border-bottom-right-radius : inherit;
color : $panel-footer-color; }

.box--midnight {
background-color : $color-midnight;
border-color : $color-midnight;
color : $white;

a {
color : $white; }
}

.box--lime {
background-color : $color-lime;
border-color : $color-lime;
color : $white;

a {
color : $white; }
}

.box--ketchup {
background-color : $color-ketchup;
border-color : $color-ketchup;
color : $white;

a {
color : $white; }
}

.box--sky {
background-color : $color-sky;
border-color : $color-sky;
color : $white;

a {
color : $white; }
}

.box--midnight,
.box--lime,
.box--ketchup,
.box--sky {

.box__heading,
.box__footer {
background-color : transparent;
border-bottom-width : 0; }
}

.box {

h1, h2, h3, h4, h5 {
margin-bottom : 0; }

.c-contact-number {
margin-bottom : 0; }
}

.box--help-quote {
  text-align: center;
  .box__cols h2, h3, h4{
      margin:0;
  }
}

@include bp(bp4) {
  .box--help-quote {
    text-align: left;

    .box__cols  h2, h3, h4 {
      margin-top:0.4rem;
      }
    }
}
