blockquote {
margin : ($vertical-spacing) 0;
padding-top : ($vertical-spacing);
border-top : 3px solid $border-color;

p {
@include ffoFamily();
font-weight : $font-weight-bold;
@include font-size($font-size-plus-one);
line-height : 1.3;
font-weight: bold;

@include bp(bp4) {
@include font-size($font-size-plus-two); }
}

&.quote--large {
margin : ($vertical-spacing * 2) 0;

p {
@include font-size($font-size-plus-three);

@include bp(bp4) {
@include font-size($font-size-plus-four); }
}

}

&.quote--brackets {
padding-top : 0;
border-top-width : 0;

p {
padding : 0 50px 0 60px;
background-image : url($root + 'img/quote-brackets-left.svg'), url($root + 'img/quote-brackets-right.svg');
background-position : 0 100%, 100% 0;
background-repeat : no-repeat;
background-size : 40px 100%; }
}

&.quote--marks {
padding-top : 0;
border-top-width : 0;

p {
padding : 0 40px 0 60px;
background-image : url($root + 'img/quote-mark-left.svg');
background-position : 0 0;
background-repeat : no-repeat;
background-size:40px;
}
}

small {
@include font-size($font-size); }

cite {
font-style : normal; }
}
